*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

*:focus {
  outline: 1px dashed #121212;
  padding: 2rem 6rem;
}

html {
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
}

.container {
  width: 144rem;
  margin: 0 auto;
}

@keyframes moveInLeft {
  0% {
    transform: translateX(-3rem);
    opacity: 0;
  }
  80% {
    transform: translateX(1rem);
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes moveInRight {
  0% {
    transform: translateX(3rem);
    opacity: 0;
  }
  80% {
    transform: translateX(-1rem);
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
body {
  font-family: "Roboto", sans-serif;
  font-style: normal;
  color: #121212;
  font-weight: 300;
  line-height: 150%;
}

.section-hero__title {
  font-size: 4.2rem;
  line-height: 5.04rem;
  margin-bottom: 2.4rem;
}

.section-hero__description {
  line-height: 2.4rem;
  font-size: 1.6rem;
  margin-bottom: 3.2rem;
}

.btn {
  display: block;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  background-color: #e4c1ae;
  border-radius: 100px;
  max-width: 12.8rem;
  height: 4.8rem;
  cursor: pointer;
}
.btn__link {
  text-decoration: none;
  color: #fff;
  font-weight: 700;
  font-size: 1.6rem;
}
.btn:hover {
  background-color: #b69a8b;
  transform: translateY(-0.5rem);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
}
.btn:active {
  transform: translateY(-0.2rem);
  box-shadow: 0 0.3rem 0.5rem rgba(0, 0, 0, 0.1);
}

.section-hero {
  margin: 9.6rem 23rem 0 23rem;
}
.section-hero-box {
  display: flex;
  gap: 9.5rem;
}
.section-hero-text-box {
  display: flex;
  flex-direction: column;
  width: 38rem;
  justify-content: center;
  animation: moveInRight 1s;
}
.section-hero__img {
  width: 35%;
  height: 35%;
  animation: moveInLeft 1s;
}